import { createWebHistory, createRouter } from "vue-router";
import ContentList          from "./components/ContentList.vue";
import ContentList2         from "./components/ContentList2.vue";
import MainHome             from "./components/MainHome.vue"
import ContentDetail        from "./components/ContentDetail.vue";
import PageNotFound         from "./components/PageNotFound.vue";
import ContentDetailAuthor  from "./components/ContentDetailAuthor.vue";
import ContentDetailComment from "./components/ContentDetailComment.vue";

const routes = [
  {
    path: "/contentList",
    component: ContentList
  },
  {
    path: "/contentList2",
    component: ContentList2
  },
  {
    path: "/",
    component: MainHome
  },
  {
    path: "/detail/:id",
    component: ContentDetail,
    children: [
      {
        path: "author",
        component: ContentDetailAuthor
      },
      {
        path: "comment",
        component: ContentDetailComment
      }
    ]
  },
  {
    path: "/:anything(.*)",
    component: PageNotFound
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes
});

export default router; 